/* Global box-sizing */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Full screen styles */
html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden; /* Prevents scrolling */
  font-family: Arial, sans-serif;
}


/* Loading container style */
.loading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 24px;
  text-align: center;
  background: rgba(0, 0, 0, 0.7); /* Semi-transparent black background */
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5); /* Adds a subtle shadow */
}

.loading-container div {
  margin-top: 15px;
  font-weight: bold;
  font-size: 18px;
}

/* Spinner animation */
.spinner {
  font-size: 48px;
  animation: spin 1.5s infinite linear;
  margin-bottom: 15px; /* Adds space between spinner and loading text */
}

/* Keyframes for spinner rotation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Control keys styling */
.controlKeys {
  position: absolute;
  width: 11rem;
  left: 5%;
  bottom: 5%;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  font-size: 1rem;
  color: #fff;
}

/* Title text at the bottom right */
.title {
  right: 5%;
  font-size: 0.8rem;
  color: darkgray;
  position: absolute;
  width: 40%;
  bottom: 2%;
  text-align: center;
  font-weight: 600;
}

/* Title links */
.title > a {
  color: rgb(79, 189, 249);
  text-decoration: none;
  font-weight: bold;
}

.title > a:hover {
  color: rgb(0, 160, 255);
}
